const initialState = {
	list: []
}

const tasksReducer = (state = initialState, action) => {
	switch(action.type) {
	case 'ADD_TASK':
		return Object.assign({}, state, state.list.concat(action.taskData))
	default:
		return state
	}
}

export default tasksReducer



